.nav {
    &__list {
        @include flex-v-center;
    }

    &__item {
        &:not(:last-child) {
            margin-right: 40px;
        }
    }

    &__link {
        font-weight: 40;
        font-size: 20px;
        line-height: 160%;
        color: var(--light-color);

        @include high-tablet {
            position: fixed;
            left: 0;
            top: 0;
            z-index: 500;
            overflow-y: auto;
            width: 100%;
            height: 100vh;
            background-color: var(--dark-color);
        }
    }

    @include high-tablet {
        display: none;
    }
}