.burger {
    display: none;
    position: relative;
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 10px;
    width: 44px;
    height: 44px;
    opacity: 0.4;

    @include high-tablet {
        display: block;
    }

    &__line {
        border-radius: 100px;
        position: absolute;
        top: 50%;
        left: 50%;
        height: 2px;
        width: 26px;
        transform: translateX(-50%);
        background-color: var(--light-color);
        transform: translateX(-50%) translateY(-50%);

        &:first-child {margin-top: -7px;}

        &:last-child {
            left: 8px;
            margin-top: 7px;
            width: 13px;
            transform: translateY(-50%);
        }
    }
}